import React, {Component} from 'react';

class ImageLabel extends Component {

    toggleTextBacking(e) {
        e.preventDefault();

        console.log("Here");
        this.props.toggleTextCallback();
    }

    getTextBackingTag() {
        return " text_backable " + (this.props.textBacking ? " text_backed" : "");
    }

    render() {
        return (
            <div id="corner_tag">
                <p id="image_tag_text"
                   className={"small_text" + this.getTextBackingTag()}> Image generated from <a
                    href="https://unsplash.com/"
                    target="_blank" rel="noopener noreferrer">Unsplash</a></p>
                <a className={"small_text" + this.getTextBackingTag()} onClick={e => this.toggleTextBacking(e)}>Toggle
                    text
                    backing</a>
            </div>
        )
    }
}

export default ImageLabel