import React, {Component} from 'react';
import '../Stylesheets/App.css';
import '../Stylesheets/Title.css';
import '../Stylesheets/ImageLabel.css';

import Title from './Title';
import ImageLabel from './ImageLabel';

class App extends Component {

    constructor(props) {
        super(props);

        let w = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
        // let h = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

        App.setBackgroundImage(w);

        this.state = {
            "textBacking": false
        };

        // this.toggleTextBacking.bind(this);
    }

    toggleTextBacking() {
        let newState = !(this.state.textBacking);

        this.setState({
            "textBacking": newState
        })
    }

    static setBackgroundImage(screenWidth) {
        let dim = "1920x1080";  // 1080p

        if (screenWidth > 2560) {
            dim = "3840x2160";  // 4k
        }

        if (screenWidth > 1920) {
            dim = "2560x1440";  // 1440p
        }

        document.body.style.backgroundImage = "url(https://source.unsplash.com/" + dim + "/?+mountain)";
    }

    render() {
        return (
            <div id="page_wrapper">
                <Title textBacking={this.state.textBacking}/>
                <ImageLabel textBacking={this.state.textBacking}
                            toggleTextCallback={this.toggleTextBacking.bind(this)}/>
            </div>
        );
    }
}

export default App;
